:root {
  --contianerweb-l: 1200px;
  --contianerweb-m: 1064px;
  --contianerweb-s: 704px;
}

.Header {
  width: 100vw;
  background: #ffffff60;
  backdrop-filter: blur(10px);
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 999;
}

.Header_Container {
  width: var(--contianerweb-l);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Header_LOGO {
  font-size: 24px;
  font-weight: 700;
}

.Header_LOGO > img {
  width: 103px;
  height: 32px;
}

.Header_RightContainer {
  display: flex;
  gap: 16px;
}

.Header_Profile {
  background-image: url(https://pds.joongang.co.kr/news/component/htmlphoto_mmdata/201611/15/htm_2016111510552285703.jpg);
  background-size: cover;
  background-position: center;
  width: 48px;
  height: 48px;
  border-radius: 99999px;
}

.Header_Container > a {
  text-decoration: none;
  color: #333333;
}

@media (max-width: 1500px) {
  .Header_Container {
    width: var(--contianerweb-m);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media (max-width: 1200px) {
  .Header_Container {
    width: var(--contianerweb-s);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
